// app/pratos/page.tsx
import UnitsSection from '@/components/UnitsSection/UnitsSection';
import Header from '@/components/Header/Header';
import Footer from '@/components/Footer/Footer';
import CategoryCard from '@/components/CategoryCard/CategoryCard';

export default function PratosPage() {
    const categoriasPratos = [
        { title: 'Entradas', image: '/imagens/categorias/entradas.jpg', href: '/pratos/entradas' },
        { title: 'Principais', image: '/imagens/categorias/principais.jpg', href: '/pratos/principais' },
        { title: 'Sobremesas', image: '/imagens/categorias/sobremesas.jpg', href: '/pratos/sobremesas' },
    ];

    const categoriasBebidas = [
        { title: 'Vinhos', image: '/imagens/categorias/vinhos.jpg', href: '/bebidas/vinhos' },
        { title: 'Refrigerantes', image: '/imagens/categorias/refrigerantes.jpg', href: '/bebidas/refrigerantes' },
        { title: 'Sucos', image: '/imagens/categorias/sucos.jpg', href: '/bebidas/sucos' },
    ];

    return (
      <div className="min-h-screen bg-gray-50">
        {/* Seção de Pratos */}
        <section className="container mx-auto px-4 py-12">
          <h2 className="mb-8 text-4xl font-bold text-emerald-700">Categorias de Pratos</h2>
          <div className="grid grid-cols-1 gap-8 md:grid-cols-3">
            {categoriasPratos.map((categoria) => (
              <CategoryCard
                key={categoria.title}
                title={categoria.title}
                image={categoria.image}
                href={categoria.href}
              />
            ))}
          </div>
        </section>
  
        {/* Seção de Bebidas */}
        <section className="container mx-auto px-4 py-12">
          <h2 className="mb-8 text-4xl font-bold text-emerald-700">Categorias de Bebidas</h2>
          <div className="grid grid-cols-1 gap-8 md:grid-cols-3">
            {categoriasBebidas.map((categoria) => (
              <CategoryCard
                key={categoria.title}
                title={categoria.title}
                image={categoria.image}
                href={categoria.href}
              />
            ))}
          </div>
        </section>
        <UnitsSection />
        <Footer />
      </div>
    );
}